    import service from '../../service.js';
    import mInput from '../../components/m-input.vue';

    export default {
        components: {
            mInput
        },
        data() {
            return {
                email: '',
				pass:'', 
				passagain:''
            }
        },
        methods: {
            findPassword() {
                /**
                 * 仅做示例
                 */
                if (this.email.length < 3 || !~this.email.indexOf('@')) {
                    uni.showToast({
                        icon: 'none',
                        title: '邮箱地址不合法',
                    });
                    return;
                }
				uni.showLoading({
					title: '提交中',
					mask: true
				});
				this.$http.lostpass({
					email:this.email,
					pass:this.pass,
					passagain:this.passagain
				}).then(res => {
					uni.hideLoading()
					uni.showToast({
					    icon: 'none',
					    title: res.data,
					    duration: 3000
					});
					if (res.data == '修改成功') {
						uni.navigateTo({
							url: '../login/login'
						});
					}
				})
                
            }
        }
    }
